.container {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.card {
  width: 300px;
  height: 340px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 14px;
  border-radius: 10px;
  background-color: #fff;
}

.cardTitle {
  margin: 0 0 20px 0;
  font-size: 1.3em;
}

.cardForm {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

.bottom {
  margin-top: 14px;

  :global(.dark) & {
    color: white;
  }
}

.loginLink {
  margin-left: 12px;
  color: #007ab8;
  font-size: 1.1em;
  font-weight: 700;
}